@import url("https://fonts.googleapis.com/css?family=Bree+Serif|Poppins|Source+Sans+Pro|Montserrat:400,900&display=swap");

$color-fondo-titulos: #C4C4C4;
$color-titulos:#005677;;
$fondo-color-body:#E6E6E6;
$fondo-color-footer:#005677;
$color-texto-footer: #FFFFFF;

body {  
  font-family: Arial, Helvetica, 'Times New Roman', Times, serif;  
}  
.cabecera{
  background-color: $fondo-color-body;
  background-image: url('../img/wallpaper.jpg');
  background-repeat: no-repeat;
  background-size: cover;  
  height: 200px;
  margin-bottom: 6rem;
  .contenedorImagen{
    display: flex;    
    justify-content: center;    
    img{    
      height: auto;    
      margin: 21px auto;
      width: 100px;    
    }
  }               
}
.titulo-principal, .tituloSeccion{
  background-color: $color-fondo-titulos;
  color: $color-titulos; 
  text-align: center;
  text-transform: uppercase;
}
.tituloSeccion{
  text-align: left;
}
section{
 .dato, .periodo{
  font-style: italic;
  font-weight: bold;
  text-align: justify;
 }
}
.periodo{
  font-style: normal;
}
article{
  .periodo{
    font-style: normal;
  }
  .textoIndentado{
    text-indent: 5rem;
  }        
}
footer{
  align-items: center;
  background-color:$fondo-color-footer; 
  color:$color-texto-footer; 
  display: flex;
  height: 37px;
  justify-content: center;        
  margin-top: 6rem;
  .pie-batman{
    font-size: 17px;
    font-weight: 700;                    
    text-transform: uppercase;        
  }
  img{
    background-color: transparent;
    height: 20px;
    margin-left: 1rem;    
    width: 20px;
  }
}